import { ADD_PRODUCT } from '../actions';

const initialState = {
    products:{
        listProducts:[]
    },
};

export const product = (state = initialState, action ) =>{
    switch(action.type){
        case ADD_PRODUCT:
            return {...state,
                products: { ...state.products.listProducts,
                    listProducts: action.payload.products
                }
            };
        default: 
            return state;
    }
}