import React, {Component } from 'react';

import '../../../../css/home.css';
import '../../../../css/aurora-pack.min.css';
import '../../../../css/aurora-theme-base.min.css';
import '../../../../css/urku.css';

class Card extends Component{
    render(){
        return(
            <a href="portfolio-item.html" className="rk-item ae-masonry__item">
                <img src={`data:${this.props.image.contentType};base64,${this.props.image.image}`} alt={this.props.image.description}/>
                <div className="item-meta">
                    <h2>{this.props.image.name}</h2>
                    <p>{this.props.image.description}</p>
                </div>
            </a>
        );
    }
}

export default Card;