import React, {Component} from 'react';
import { connect } from 'react-redux';
import { PropTypes} from 'prop-types';
import CircularProgress from '@material-ui/core/CircularProgress';

import { fetchProducts } from '../../../actions/index';

import '../../../css/home.css';
import '../../../css/aurora-pack.min.css';
import '../../../css/aurora-theme-base.min.css';
import '../../../css/urku.css';

import Card from './card';

class Home extends Component{

    componentWillMount(){
        this.props.getProducts(null);
        
    }

    render(){
        return(
            <section >
                    {this.props.myProducts.length === 0 ? 
                <div style={{ marginTop: '100px', alignItems: 'center',display: 'flex', justifyContent: 'center'}}>
                    <CircularProgress color="inherit" size={60}></CircularProgress>
                </div> : 
                <section className={`ae-container-fluid ae-container-fluid--inner rk-portfolio marginTop15`}>
                    <div  className="ae-masonry ae-masonry-md-2 ae-masonry-xl-4">
                    {
                        
                    
                    this.props.myProducts.map((image, index)=>{
                    return(
                        <Card key={index} image={image}></Card>
                        );
                    })
                    }
                    </div>
                </section>}
            </section>
        );
    }
}

Home.propTypes = {
    myProducts: PropTypes.array,
    getProducts: PropTypes.func
}

const mapStateToProps = (state) => ({
    myProducts: state.products.listProducts,
});

const mapDispatchToProps = (dispatch) => ({
    getProducts : value => dispatch(fetchProducts(value))
});

export default connect(mapStateToProps, mapDispatchToProps)(Home);